import {Component, Input, OnInit, ViewChild} from '@angular/core';
import * as moment from 'moment';
import {MatSidenav} from '@angular/material';
import * as Highcharts from 'highcharts';
import {nFormatter} from '../../_shared/utils';

@Component({
  selector: 'pp-planner',
  templateUrl: './planner.component.html',
  styleUrls: ['./planner.component.scss']
})
export class PlannerComponent implements OnInit {

  constructor() {
  }

  @ViewChild('sidenav', {static: false}) sidenav: MatSidenav;
  @Input() name: string;

  shouldRun = false;
  reason = '';

  readonly selectedItem = 'selected_item';
  lastSaved: string = null;
  date = new Date();
  selectedToolbarOption: string = null;
  toolbarOptions = [
    'settings',
    'trending_up',
    'help_outline',
    'get_app',
  ];

  static initChart() {
    const data = [];

    for (let i = 0; i < 3000; i++) {
      data.push([i, Math.log(i + 1)]);
    }

    Highcharts.chart('sidenav-chart-container', {

      title: {
        text: ''
      },

      credits: {
        enabled: false
      },

      xAxis: {
        tickInterval: 1,
        title: {
          text: ''
        },
        labels: {
          formatter() {
            return this.value ? `$${nFormatter(this.value)}K` : '0';
          }
        },
      },

      yAxis: {
        title: {
          text: ''
        },
        labels: {
          formatter() {
            return this.value ? `$${nFormatter(this.value)}K` : '0';
          }
        },
        minorTickInterval: 0.1
      },

      tooltip: {
        headerFormat: '<b>{series.name}</b><br />',
        pointFormat: '{point.x}'
      },

      series: [
        {
          name: '',
          type: 'line',
          data,
          pointStart: 1
        }
      ]
    });
  }
  ngOnInit() {
    this.selectedToolbarOption = localStorage.getItem(this.selectedItem);
    this.lastSaved = moment(this.date).fromNow();
    setInterval(() => this.lastSaved = moment(this.date).fromNow(), 1000);
    this.shouldRun = [/(^|\.)plnkr\.co$/, /(^|\.)stackblitz\.io$/].some(h => h.test(window.location.host));
    PlannerComponent.initChart();
  }

  selectItem(item: string) {
    if (this.selectedToolbarOption && this.selectedToolbarOption === item) {
      this.close();
    } else {
      this.selectedToolbarOption = item;
      localStorage.setItem(this.selectedItem, item);
      console.log('Pushbar item: %c' + this.selectedToolbarOption, 'color:#1e88e5');
    }
  }

  close() {
    setTimeout(() => {
      this.selectedToolbarOption = null;
      localStorage.removeItem(this.selectedItem);
      console.log('Pushbar: %cclosed', 'color:red');
    });
  }

  closeSidenav(reason: string) {
    this.reason = reason;
    this.sidenav.close();
    console.log('Sidepanel: %cclosed', 'color:red');
  }

  open(data) {
    this.name = data;
    this.sidenav.open();
    console.log('Sidepanel: %copened', 'color:green');
  }
}
