import {AfterViewInit, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import * as Highcharts from 'highcharts';
import {MatTab} from '@angular/material';
import {nFormatter} from '../../../_shared/utils';

@Component({
  selector: 'pp-performance-forecast',
  templateUrl: './performance-forecast.component.html',
  styleUrls: ['./performance-forecast.component.scss']
})
export class PerformanceForecastComponent implements AfterViewInit {
  @Output() openSideNav: EventEmitter<string> = new EventEmitter<string>(false);
  @Input() name: string;

  forecast = 1;

  constructor() {
  }

  static initChart() {
    const data = [
      // [60, 47.78],
      // [30, 45.35],
      // [20, 42.98],
      // [15, 40.66],
      // [12, 38.4],
      // [10, 36.19],
      // [6, 27.93],
      // [5, 24.13],
      // [4, 18.85],
      // [3, 11.17],
    ];

    for (let i = 0; i < 8000; i++) {
      data.push([i, Math.log(i + 5)]);
    }

    Highcharts.chart('container', {

      title: {
        text: ''
      },
      chart: {
        type: 'spline'
      },
      credits: {
        enabled: false
      },

      xAxis: {
        tickInterval: 1,
        labels: {
          formatter() {
            return `$${nFormatter(this.value)}`;
          }
        },
        plotLines: [{
          value: 2000,
          color: '#333',
          dashStyle: 'Dash',
          width: 2,
          label: {
            text: ''
          }
        }]
      },

      yAxis: {
        minorTickInterval: 0.1,
        title: {
          text: 'Conversion'
        },
        labels: {
          formatter() {
            return `${this.value}K`;
          }
        },

        plotLines: [{
          value: 7.5,
          color: '#333',
          dashStyle: 'Dash',
          width: 2,
          label: {
            text: 'Target'
          }
        }]
      },

      tooltip: {
        headerFormat: '<b>{series.name}</b><br />',
        pointFormat: '{point.y}'
      },

      series: [
        {
          name: 'Spend',
          type: 'line',
          data,
          pointStart: 1
        }
      ]
    });
  }

  ngAfterViewInit() {
    PerformanceForecastComponent.initChart();

    setTimeout(() => {
      const resizeEvent = new Event('resize');
      window.dispatchEvent(resizeEvent);
      setTimeout(() => {
        window.dispatchEvent(resizeEvent);
      }, 100);
    }, 100);
  }
}
