import {Component, EventEmitter, OnInit, Output, Inject} from '@angular/core';
import {FormControl} from '@angular/forms';
import {NgxDrpOptions, PresetItem, Range} from 'ngx-mat-daterange-picker';
import {MatDialog} from '@angular/material';
import {PlanSettingsDialogComponent} from '../plan-settings-dialog/plan-settings-dialog.component';

@Component({
  selector: 'pp-plan-settings',
  templateUrl: './plan-settings.component.html',
  styleUrls: ['./plan-settings.component.scss']
})
export class PlanSettingsComponent implements OnInit {
  dateFrom = new FormControl(new Date());
  @Output() closePanel: EventEmitter<void> = new EventEmitter();

  range: Range = {fromDate: new Date(), toDate: new Date()};
  options: NgxDrpOptions;
  presets: Array<PresetItem> = [];

  constructor(public dialog: MatDialog) {
  }

  openDialog(): void {
    console.log('The dialog was: %copened ', 'color:green');
    const dialogRef = this.dialog.open(PlanSettingsDialogComponent, {
      width: '600px',
      data: {}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was: %cclosed ', 'color:red');
    });
  }

  ngOnInit() {
    this.options = {
      placeholder: 'Select a date range',
      presets: this.presets,
      range: this.range,
      format: 'mediumDate'
    };
  }

  updateRange(range: Range) {
    this.range = range;
  }
}
